<template>
  <div class="main">
    <div>
      
      <!-- 二级路由插座 -->
      <router-view></router-view>

    </div>
    
    <van-tabbar v-model="tabBarName"  @change="toggleTabBar">
      <van-tabbar-item v-for="(item, index) in tabBarData" :key="index" :icon="item.icon">{{item.title}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tabBarName: 0,
        
        //底部导航数据
        tabBarData: [
          {
            icon: 'wap-home-o',
            title: '主页',
            routeName: 'Menu'
          },
          {
            icon: 'orders-o',
            title: '订单',
            routeName: 'Order'
          },
          {
            icon: 'shopping-cart-o',
            title: '购物车',
            routeName: 'Shopcart'
          },
          {
            icon: 'contact',
            title: '我的',
            routeName: 'My'
          }
        ]
      };
    },

    methods: {

      //切换底部导航
      toggleTabBar(index) {
        
        this.$router.push({name: this.tabBarData[index].routeName});
      }
    }
  }
</script>

<style lang="less" scoped>
.van-hairline--top-bottom{
  // background-color: rgb(243,241,242);
  border-top: 1px solid rgb(243,241,242);
}
.van-tabbar-item--active{
  color:rgb(1,115,211)
}
</style>